import React, { useState } from 'react';

import styles from './index.less';

interface TabBtnOptionsProps {
  label: string;
  value: string | number;
}

interface TabBtnProps {
  options: TabBtnOptionsProps[];
  onChange?: Function;
}

const TabBtn: React.SFC<TabBtnProps> = (props) => {
  const { options = [], onChange } = props;
  const [activeIndex, setActiveIndex] = useState<number>(0);

  const handleChange = (index: number, value: string | number) => {
    setActiveIndex(index);
    if (onChange) {
      onChange(value);
    }
  }

  return (
    <div className={styles.tabBtnGroups}>
      {
        options.map((i: TabBtnOptionsProps, n: number) => {
          return <span
            key={i.value} 
            className={n === activeIndex ? styles.btnItem + ' ' + styles.active : styles.btnItem} 
            onClick={()=>handleChange(n, i.value)}>
              {i.label}
            </span>
        })
      }
    </div>
  ) 
}

export default TabBtn;