import React, { useEffect, useState } from 'react';
import moment from 'moment';

import { DecryptDes, GetCookie } from '../../../../utils/func';
import { fetchTodoAdd } from '../../../../services/detail';
import Button from '../../../../components/Button';
import DatePicker from '../../../../components/DatePicker';
import Textarea from '../../../../components/Textarea';
import Picker from '../../../../components/Picker';
import Mask from '../../../../components/Mask';
import ActiveInfo from '../../../../components/ActiveInfo';
import calendar from '../../../../assets/icons/calendar_gray.svg';
import styles from './index.less';
import { Toast } from 'antd-mobile';

interface AddDealtProps {
  visible: boolean;
  onCancel: Function;
  onOk?: Function;
}

const remarkOptions = [
  {
    label: '日程开始时',
    value: 0
  },
  {
    label: '5分钟前',
    value: 5
  },
  {
    label: '15分钟前',
    value: 15
  },
  {
    label: '1小时前',
    value: 60
  },
  {
    label: '1天前',
    value: 86400
  },
]

const AddDealt: React.FC<AddDealtProps> = (props) => {
  const { visible, onCancel, onOk } = props;
  const [todoTimeDate, setTodoTimeDate] = useState<string>('');
  const [todoTimeTime, setTodoTimeTime] = useState<string>('');
  const [msg, setMsg] = useState<string>('');
  const [remarkTime, setRemarkTime] = useState<number>(0);

  const handleSubmit = () => {
    fetchTodoAdd({
      user_id: sessionStorage.getItem('userId'),
      todo_time: new Date(`${todoTimeDate} ${todoTimeTime}`).getTime() / 1000,
      msg,
      remind_time: (new Date(`${todoTimeDate} ${todoTimeTime}`).getTime() - remarkTime * 60000) / 1000,
      external_user_id: DecryptDes(GetCookie('externaluserid')),
      complete_status: 0,
    }).then((res: any) => {
      if (res.code === 200) {
        Toast.success('添加成功');
        onCancel()
        onOk()
      }
    })
  }

  return (
    <Mask title="添加待办" visible={visible} onCancel={onCancel}>
      <div style={{ padding: '15px 20px' }}>
        <ActiveInfo title="日期" value={
          <div className={styles.dateContainer}>
            <DatePicker mode="date" suffixIcon={calendar} onChange={val => setTodoTimeDate(val)} />
          </div>
        } />
        <ActiveInfo title="时间" value={
          <div className={styles.dateContainer}>
            <DatePicker mode="time" suffixIcon={calendar} onChange={val => setTodoTimeTime(val)} />
          </div>
        } />
        <ActiveInfo alignItems="flex-start" title="事项" value={
          <div style={{ marginBottom: '5px' }}>
            <Textarea showCount onChange={val => setMsg(val)} />
          </div>
        } />
        <ActiveInfo title="提醒" value={
          <div className={styles.dateContainer}>
            <Picker data={remarkOptions} onChange={(val: number) => setRemarkTime(val[0])} />
          </div>
        } />
        <Button type="primary" onClick={handleSubmit}>确定</Button>
      </div>
    </Mask>
  )
}

export default AddDealt;